@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Orbitron);
Body {
    background-color: #26242D;
}

.Head {
    display: -webkit-flex;
    display: flex;
}

.Head button {
    margin-left: 5.7rem;
    margin-top: 0.6rem;
    background-color: #7d96f3;
    height: 1.5rem;
    border: 0px;
    cursor: pointer;
    border-radius: 8px;
}

.Head button img {
    height: 1.2rem;
}

.Title {
    margin-top: 0.8rem;
    margin-bottom: 0.5rem;
    margin-left: 5%;
    font-family: "Lobster", cursive;
    font-weight: 300;
    font-size: 2rem;
}

.Title .TimerIcon {
    width: 1.3rem;
}

.Card {
    margin-right: auto;
    margin-left: auto;
    margin-top: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 300px;
    background-color: #7d96f3;
    border-radius: 8px;
}

.TimerBox {
    margin-right: auto;
    margin-left: auto;
    font-size: 3em;
    border-radius: 6px;
    font-family: "Orbitron";
}

.TimerBox p {
    margin-right: auto;
    margin-left: auto;
    margin-top: 1.3rem;
    margin-bottom: 0.5rem;
}

.TimerBtns {
    margin-right: auto;
    margin-left: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: -webkit-flex;
    display: flex;
    height: 3rem
}

.Btn {
    cursor: pointer;
    background-color: #7d96f3;
    border: 0px;
    width: 2rem;
    border-radius: 100%;
    margin-left: 1.2rem;
    margin-right: 1.2rem;
}

.Btn img {
    height: 1.5rem;
    background-color: #7d96f3;
}

.Btn .Pause {
    height: 1.2rem;
}

.Btn .Play {
    height: 1.8rem;
}
.MenuHead {
    margin-right: auto;
    display: -webkit-flex;
    display: flex;
}

.MenuCard {
    margin-right: auto;
    margin-left: auto;
    margin-top: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 300px;
    border-radius: 8px;
    background-color: #A5B9FF;
}

.MenuHead h2 {
    margin-top: 1rem;
    margin-left: 2.8rem;
    font-weight: 545;
    font-size: 1.5rem;
}

.MenuHead button {
    /* margin-left: 6rem; */
    background-color: #A5B9FF;
    height: 1.5rem;
    cursor: pointer;
    margin-top: 1.3rem;
    margin-right: 0.5rem;
    margin-left: 0px;
    border: 0px;
}

.MenuHead button img {
    width: 1rem;
}

.Options {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-left: 1rem;
}

.Options button {
    cursor: pointer;
    background-color: #8099f5;
    border: 0px;
    width: 1.5rem;
    height: 1.3rem;
    border-radius: 20%;
    margin-left: 0.2rem;
    margin-right: 0.1rem;
    margin-bottom: 0.5rem;
}

.Options input {
    margin-bottom: 0.7rem;
    cursor: pointer;
}

.Option label {
    cursor: pointer;
}

.Option button{
    font-weight: 900;
}

.Times {
    margin-right: auto;
    margin-bottom: 1rem;
}

.Times h3 {
    margin-bottom: 0.6rem;
    font-weight: 500;
}

.restoreDefaultTime {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
}

.restoreDefaultTime button {
    border: 0px;
    cursor: pointer;
    background-color: #8099f5;
    border-radius: 5px;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
}
